.experience .timeline-element-icon {
  visibility: visible;
  background: #fff;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  margin-left: -30px;
  animation: .6s bounce-1;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes bounce-1 {
  0% {
    opacity: 0;
    transform: scale(.5);
  }

  60% {
    opacity: 1;
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

